.container__navbar {
  display: flexbox;
  align-items: center;
  background: url(/static/media/navBack.5c2b4fe1.gif)
    no-repeat center center fixed;
  background-size: cover;
  /* background-color: transparent; */
  padding: 25px;
  width: 100%;
  margin-bottom: 20px;
}

@media only screen and (max-width: 900px) {
  .container__navbar {
    display: flexbox;
    align-items: center;
    background: url(/static/media/navBack.5c2b4fe1.gif)
      no-repeat center center fixed;
    background-size: cover;
    /* background-color: transparent; */
    padding: 25px;
    width: 100rem;
    margin-bottom: 20px;
  }
}

.navbar-title {
  color: transparent;
  font-size: 28px;
  margin-bottom: -50px;
  text-align: right;
}

.navbar-menu {
  border-radius: 25px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: inline-flex;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  align-items: center;
  padding: 0 20px;
  margin: 50px 0 0 0;
}
.navbar-menu li {
  list-style: none;
  color: white;
  font-family: sans-serif;
  font-weight: bold;
  padding: 12px 60px;
  margin: 0 8px;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
}
.navbar-menu li::before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  transition: 0.6s;
  border-radius: 25px;
}
.navbar-menu li:hover {
  color: black;
}
.navbar-menu li:hover::before {
  background: linear-gradient(to bottom, #e8edec, #d2d1d3);
  box-shadow: 0px 3px 20px 0px black;
  transform: scale(1.2);
}

@media only screen and (max-width: 1000px) {
  .navbar-menu {
    border-radius: 25px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: inline-flex;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    align-items: center;
    padding: 0 0px;
    margin: 50px 0 0 0;
  }
  .navbar-menu li {
    list-style: none;
    color: white;
    font-family: sans-serif;
    font-weight: bold;
    padding: 12px 10px;
    margin: 0 1px;
    position: relative;
    cursor: pointer;
    white-space: nowrap;
  }
  .navbar-menu li::before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    transition: 0.6s;
    border-radius: 25px;
  }
  .navbar-menu li:hover {
    color: black;
  }
  .navbar-menu li:hover::before {
    background: linear-gradient(to bottom, #e8edec, #d2d1d3);
    box-shadow: 0px 3px 20px 0px black;
    transform: scale(1.2);
  }
}

.search-button {
  background-image: linear-gradient(
    to right,
    rgb(134, 97, 209) 0%,
    #8f75c2 51%,
    #693ac0 100%
  );
}
.search-button {
  margin: 0 auto;
  padding: 10px 70px;
  margin-top: 0px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  font-family: "Finger Paint";
  box-shadow: 0 0 20px #a385df;
  border-radius: 10px;
  display: block;
  outline: none;
}

.search-button:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.search-label {
  color: white;
  font-family: "Finger Paint", cursive;
  font-size: 1.2rem;
  margin-left: 2rem;
  margin-top: 0.1rem;
  display: block;
  transition: all 0.3s;
  transform: translateY(0rem);
}

.search-input {
  color: #8661d1;
  font-size: 1rem;
  font-family: "Finger Paint", cursive;
  margin: 0 auto;
  padding: 1rem 0.5rem;
  border-radius: 0.3rem;
  background-color: rgb(255, 255, 255);
  border: none;
  width: 56rem;
  height: 4rem;
  display: block;
  border-bottom: 1rem solid transparent;
  transition: all 0.3s;
  outline:none;
}

@media only screen and (max-width: 900px) {
  .search-input {
    color: #333;
    font-size: 1.2rem;
    margin: 0 auto;
    padding: 1rem 0.5rem;
    border-radius: 0.6rem;
    background-color: rgb(255, 255, 255);
    border: none;
    width: 100%;
    display: block;
    border-bottom: 1rem solid transparent;
    transition: all 0.3s;
  }
}

.search-input:-ms-input-placeholder + .search-label {
  opacity: 0;
  color: white;
  visibility: hidden;
  transform: translateY(-4rem);
}

.search-input:placeholder-shown + .search-label {
  opacity: 0;
  color: white;
  visibility: hidden;
  transform: translateY(-4rem);
}

